<template lang="pug">
  .kt-panel
    .panel-head {{$route.params.id === 'new' ? '': product.name}}
    .panel-body
      el-form.login-form(ref="productForm", :model="product",label-width="115px", :rules="rules")
        .panel-body-title 
        .form-block.clfix
          .left
            el-form-item.el-input--xs(label="", prop="name")
              el-input(placeholder='A', v-model="product.name")
            el-form-item.el-input--xs(label="", prop="consignee_name")
              el-autocomplete(v-model="product.consignee_name", :fetch-suggestions="queryConsigneeSearch", placeholder="", @select="handleConsigneeSelect")
            el-form-item.el-input--xs(label="", prop="product_short_name")
              el-input(placeholder='', v-model="product.product_short_name")
            el-form-item.el-input--xs(label="", prop="product_code")
              el-input(placeholder='', v-model="product.product_code")
            el-form-item.el-input--xs(label="", prop="annual_rate")
              el-input(placeholder='', v-model="product.annual_rate")
               template(slot='append') %
            el-form-item.el-input--xs(label="", prop="value_at")
              el-date-picker(v-model="product.value_at", type="date", placeholder="", :picker-options="valueAtOptions", @change="calculateSustained")
            el-form-item.el-input--xs(label="", prop="due_at")
              el-date-picker(v-model="product.due_at", type="date", placeholder="", :picker-options="valueAtOptions", @change="calculateSustained")
            el-form-item.el-input--xs(label="", prop="sustained")
              el-input(placeholder='=-',:disabled="true", v-model.number="product.sustained")
               template(slot='append') 
          .right
            el-form-item.el-input--xs(label="", prop="allocated_amount")
              el-input(placeholder='50000', v-model="product.allocated_amount")
                template(slot='append') 
            el-form-item.el-input--xs(label="", prop="subscription_amount")
              el-input(placeholder='50000', v-model="product.subscription_amount")
                template(slot='append') 
            el-form-item.el-input--xs(label="", prop="raw_risk_level")
              el-select(v-model="product.raw_risk_level", placeholder="")
                el-option(v-for='item in riskLevelOptions', :label='item.label', :value='item.value')
            el-form-item.el-input--xs(label="", prop="published_start_at")
              el-date-picker(v-model="product.published_start_at", type="date", placeholder="", :picker-options="valueAtOptions", @change="calculateAllocatedSustained")
            el-form-item.el-input--xs(label="", prop="published_end_at")
              el-date-picker(v-model="product.published_end_at", type="date", placeholder="", :picker-options="valueAtOptions", @change="calculateAllocatedSustained")
            el-form-item.el-input--xs(label="", prop="reserved_sustained")
              el-input(placeholder='=-',:disabled="true", v-model="product.reserved_sustained")
               template(slot='append') 
            el-form-item.el-input--xs(label="", prop="settle_deadline_minutes")
              el-autocomplete(v-model="product.settle_deadline_minutes", :fetch-suggestions="querySDMinutesSearch", placeholder="", @select="handleSDMinutesSelect")
              //- el-input(placeholder='50000', v-model.number="product.allocated_amount", icon="time")
              //- el-time-select(v-model="product.settle_deadline_minutes", placeholder="", :picker-options="{start: '00:00', step: '00:01', end: '23:30'}", @change="settleDeadlineMinuteChange")
              //- | {{product.settle_deadline_minutes}}
              //- el-time-picker(v-model="product.settle_deadline_minutes", placeholder="", :picker-options="{selectableRange: '18:30:00 - 20:30:00'}")
            el-form-item.el-input--xs(label="", prop="days_of_year")
               el-select(v-model="product.days_of_year", placeholder="")
                el-option(v-for='item in daysOfYearOptions', :label='item.label', :value='item.value')
        .panel-body-title 
        .form-block.clfix
          .left
            el-form-item.el-input--xs(label="", prop="consignee_bank_account_name")
              el-input(placeholder='xxx', v-model="product.consignee_bank_account_name")
            el-form-item.el-input--xs(label="", prop="consignee_bank_account")
              el-input(placeholder='0200 0000 2222 0000 2222', v-model="product.consignee_bank_account")
            el-form-item.el-input--xs(label="", prop="consignee_bank_name")
              el-input(placeholder='xxx', v-model="product.consignee_bank_name")
        .form-footer
          el-button.input(type="primary", size="xs", @click="submitForm") 
          el-button.input(type="minor", size="xs", @click="cancelForm") 
</template>

<script>
import {
  Form,
  FormItem,
  DatePicker,
  TimePicker,
  TimeSelect,
  Autocomplete,
  MessageBox,
  Message,
  Select,
  Loading,
  Option,
  Input,
  Button
} from 'element-ui'
import moment from 'moment'
import _ from 'lodash'
import {
  consignees,
  productOne,
  essentialInformation
} from '../../../common/resources.js'
import {
  updateCrumbs
} from '../../../common/crossers.js'

let loadingInstance

export default {
  components: {
    ElForm: Form,
    ElFormItem: FormItem,
    ElInput: Input,
    ElSelect: Select,
    ElOption: Option,
    ElDatePicker: DatePicker,
    ElTimePicker: TimePicker,
    ElTimeSelect: TimeSelect,
    ElAutocomplete: Autocomplete,
    ElButton: Button
  },

  async mounted() {
    let {
      id
    } = this.$route.params

    let consigneesData = await consignees.get().then(res => res.json())
    this.consignees = _.map(consigneesData.consignees, v => {
      return {
        id: v.id,
        value: v.name
      }
    })

    if (id && id !== 'new') {
      let data = await essentialInformation.get({
        virtual_asset_id: id
      }).then(res => res.json())

      setTimeout(() => {
        updateCrumbs.$emit('update-crumbs', [{
          id: 'productName',
          name: data.virtual_asset.name
        }])
      }, 100)

      data.virtual_asset.consignee_name = _.get(_.find(this.consignees, c => c.id === data.virtual_asset.consignee_id), 'value')
      if (data.virtual_asset.annual_rate) data.virtual_asset.annual_rate *= 100

      this.product = data.virtual_asset
    } else {
      setTimeout(() => {
        updateCrumbs.$emit('update-crumbs', [{
          id: 'productName',
          name: ''
        }])
      }, 100)
    }
  },

  methods: {
    // 
    queryConsigneeSearch(queryString, cb) {
      var results = queryString ? this.consignees.filter(v => v.value.includes(queryString)) : []

      //  callback 
      cb(results)
    },

    // 
    handleConsigneeSelect(item) {
      this.product.consignee_id = item.id
      this.reValidate(['consignee_name'])
    },

    // 
    querySDMinutesSearch(queryString, cb) {
      var results = queryString ? this.sdMinutes.filter(v => v.value.includes(queryString)) : this.sdMinutes
      cb(results)
    },

    // 
    handleSDMinutesSelect(item) {
      this.reValidate(['settle_deadline_minutes'])
    },

    // 
    reValidate(fields) {
      _.map(fields, v => {
        this.$refs.productForm.validateField(v)
      })
    },

    // 
    calculateSustained() {
      if (this.product.value_at && this.product.due_at) {
        this.reValidate(['value_at', 'due_at', 'published_start_at'])
        this.product.sustained = moment(this.product.due_at).diff(moment(this.product.value_at), 'days')
      }
    },

    // 
    calculateAllocatedSustained() {
      if (this.product.published_start_at && this.product.published_end_at) {
        this.reValidate(['value_at', 'published_start_at', 'published_end_at'])
        this.product.reserved_sustained = moment(this.product.published_end_at).diff(moment(this.product.published_start_at), 'days')
      }
    },

    // , 
    // numberTrim(e, value) {
    //   if (value === '') return
    //   setTimeout(() => {
    //     e.target.value = value
    //   }, 100)
    // },

    // 
    // settleDeadlineMinuteChange(value) {
    //   this.product.settle_deadline_minutes = value
    //   console.log(value)
    // },

    // 
    submitForm() {
      this.$refs.productForm.validate((valid) => {
        if (valid) {
          loadingInstance = Loading.service({
            target: this.$refs.productForm.$el
          })

          let savePromise
          if (this.$route.params.id === 'new') { // 
            savePromise = productOne.save({
              action: 'create'
            }, this.product)
          } else {
            savePromise = productOne.update({ // 
              virtual_asset_id: this.product.id,
              action: 'update'
            }, this.product)
          }

          savePromise.then(res => res.json()).then(data => {
            loadingInstance.close()
            Message({
              type: 'success',
              message: ''
            })
            this.$router.push({
              name: 'productDashboard',
              params: {
                id: this.product.id || data.id
              }
            })
          }).catch(res => {
            loadingInstance.close()
          })
        }
      })
    },

    // 
    cancelForm() {
      MessageBox.confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$router.back()
      })
    }
  },

  watch: {
    'product.settle_deadline_minutes' (newValue) {
      if (moment(newValue, 'H:mm').format('H:mm') === newValue) {
        this.product.settle_deadline_minutes = moment(newValue, 'H:mm').format('HH:mm')
      }
    }
  },

  data() {
    var _self = this

    // 
    let validateConsignee = (rule, value, cb) => {
      if (value === '') {
        cb(new Error(''))
      } else if (!this.consignees.some(v => v.value === value)) {
        cb(new Error(''))
      } else {
        cb()
      }
    }

    // 
    let validateCompareDate = (rule, value, cb) => {
      if (rule.lt && moment(value).toDate() >= moment(_.get(_self, rule.lt)).toDate()) {
        cb(new Error(`${rule.ltFieldName}`))
      } else if (rule.gt && moment(value).toDate() <= moment(_.get(_self, rule.gt)).toDate()) {
        cb(new Error(`${rule.gtFieldName}`))
      } else {
        cb()
      }
    }

    // 
    let validateTime = (rule, value, cb) => {
      if (value && rule.timeFormat && moment(value, rule.timeFormat).format(rule.timeFormat) !== value) {
        cb(new Error(`${moment().format(rule.timeFormat)}`))
      }
      cb()
    }

    // 
    let validateNumber = (rule, value, cb) => {
      if (value !== '' && value !== null && rule.pattern && !rule.pattern.test(value)) {
        cb(new Error(``))
      } else if (value !== '' && value !== null && rule.min && value < rule.min) {
        cb(new Error(`${rule.min}`))
      } else {
        cb()
      }
    }

    let sdMinutes = _.flatten(_.range(24).map(vh => {
      return _.range(4).map(vm => {
        return {
          value: moment(`${vh}:${vm * 15}`, 'H:mm').format('HH:mm')
        }
      })
    }))

    return {
      valueAtOptions: {
        /*disabledDate(time) {
          return time.getTime() < Date.now() - 8.64e7
        }*/
      },
      daysOfYearOptions: [{ // 
        label: '360 ',
        value: '1'
      }, {
        label: '365 ',
        value: '2'
      }],
      riskLevelOptions: [{ // 
        label: '',
        value: 'L'
      }, {
        label: '',
        value: 'ML'
      }, {
        label: '',
        value: 'M'
      }, {
        label: '',
        value: 'MH'
      }, {
        label: '',
        value: 'H'
      }],
      consignees: [],
      sdMinutes: sdMinutes,
      product: {
        name: '', // 
        consignee_id: '', //id
        raw_risk_level: 'L', //
        consignee_name: '', //
        product_short_name: '', // 
        product_code: '', // 
        annual_rate: null, // 
        value_at: '', // 
        due_at: '', // 
        sustained: null, // 
        days_of_year: '2', //
        subscription_amount: null, // 
        allocated_amount: null, // 
        published_start_at: '', // 
        published_end_at: '', // 
        reserved_sustained: '', // 
        settle_deadline_minutes: '17:00', // 
        consignee_bank_name: '', // 
        consignee_bank_account: '', //
        consignee_bank_account_name: '' //
      },
      rules: { // 
        name: [{
          required: true,
          message: '',
          trigger: 'blur'
        }, {
          min: 1,
          max: 50,
          message: '50',
          trigger: 'blur'
        }],
        consignee_name: [{
          required: true,
          message: '',
          trigger: 'blur'
        }, {
          validator: validateConsignee,
          trigger: 'blur'
        }],
        product_short_name: [{
          min: 0,
          max: 50,
          message: '50'
        }],
        product_code: [{
          min: 0,
          max: 50,
          message: '50'
        }],
        annual_rate: [{
          pattern: /^\d+(?:\.\d+)?$/,
          min: 0.01,
          // type: 'number',
          validator: validateNumber,
          message: ', 0.01'
        }],
        value_at: [{
          gt: 'product.published_start_at',
          lt: 'product.due_at',
          ltFieldName: '',
          gtFieldName: '',
          validator: validateCompareDate
        }],
        due_at: [{
          gt: 'product.value_at',
          gtFieldName: '',
          validator: validateCompareDate
        }],
        allocated_amount: [{
          pattern: /^\d+$/,
          message: ''
            // min: 0
        }],
        subscription_amount: [{
          pattern: /^\d+$/,
          message: ''
            // type: 'integer',
            // min: 0
        }],
        published_start_at: [{
          lt: 'product.published_end_at',
          ltFieldName: '',
          validator: validateCompareDate
        }],
        published_end_at: [{
          gt: 'product.published_start_at',
          lt: 'product.value_at',
          gtFieldName: '',
          ltFieldName: '',
          validator: validateCompareDate
        }],
        settle_deadline_minutes: [{
          validator: validateTime,
          timeFormat: 'HH:mm'
        }],
        consignee_bank_name: [{
          min: 0,
          max: 50,
          message: '50'
        }],
        consignee_bank_account: [{
          min: 0,
          max: 50,
          message: '50'
        }],
        consignee_bank_account_name: [{
          min: 0,
          max: 50,
          message: '50'
        }]
      }
    }
  }
}
</script>

<style lang="scss">
.kt-panel {
  // margin: -10px;
  .panel-head {
    height: 40px;
    line-height: 40px;
  }
}
</style>



